<template>
  <div class="feedback full-screen">
    <div class="page-panel">
      <pem-scroll-view ref="scroll" class="scroll-view">
        <div class="feedback-wrapper">
          <div class="feedback-inner" :class="{'feedback-inner-focus': isFocus}">
            <div class="feedback-header">
              <img src="./erweima@2x.png" alt="">
              <h3>为您呈上产品经理微信</h3>
              <p>您觉得项目工场哪些功能是反人类设计？请吐槽！</p>
            </div>
            <div class="feedback-content">
              <textarea placeholder="也可以直接在这里输入您的意见，感谢您对项目工场的支持！" maxlength="200" @input="input"  @focus="focusInput()" @blur="blurInput()" v-model="content"></textarea>
              <div><span>{{ remnant }}／{{ limit }}</span></div>
            </div>
            <div class="feedback-submit-wrapper">
              <pem-button text="提交" @submit="submit"/>
            </div>
          </div>
        </div>
      </pem-scroll-view>
      <pem-operation-panel @backtotop="backtotop" />
      <pem-toast ref="toast" :text="toastText"></pem-toast>
    </div>
  </div>
</template>

<script type="text/ecmascript-6">
import PemOperationPanel from 'components/pem-operation-panel/pem-operation-panel'
import PemScrollView from 'components/scroll-view/scroll-view'
import PemButton from 'components/pem-button/pem-button'
import PemToast from 'components/pem-toast/pem-toast'
import { MiniSiteApi } from 'api'
import { setTitle } from 'common/js/util'

export default {
  components: {
    PemOperationPanel,
    PemScrollView,
    PemButton,
    PemToast
  },
  data () {
    return {
      content: '',
      remnant: 200,
      limit: 200,
      toastText: '',
      isFocus: false
    }
  },
  created () {
    this._initWXShare()
    this._setTitle('项目工场-意见反馈')
  },
  methods: {
    _initWXShare () {
      this.wxShare({
        title: '为您推荐项目工场',
        content: '我正在使用项目工场，有料的BP、有趣的VC都在这儿，一起来看项目吧！',
        linkUrl: window.location.origin
      })
    },
    _setTitle (title) {
      setTitle(title)
    },
    focusInput () {
      this.isFocus = true
    },
    blurInput () {
      this.isFocus = false
    },
    backtotop () {
      this.$refs.scroll.scrollTo(0, 0, 300)
    },
    input () {
      this.content = this.content.trim()
      this.remnant = this.limit - this.content.length
      if (this.limit - this.content.length <= 0) {
        return false
      }
    },
    submit () {
      if (this._validate()) {
        this.insertFeedBack()
      }
    },
    toast (text) {
      this.toastText = text
      this.$refs.toast.show()
    },
    _validate () {
      if (this.content.length <= 0) {
        this.toast('请输入反馈内容!')
        return false
      }
      if (this.content.length > this.limit) {
        this.toast('最多可输入200字')
        return false
      }
      return true
    },
    insertFeedBack () {
      this._insertFeedBack(this.content, (data) => {
        switch (+data.resultcode) {
          case 1:
            this.toast('提交成功')
            this.content = ''
            this.input()
            setTimeout(() => {
              this.$router.goBack()
            }, 1000)
            break
          case -11:
            this.toast('用户登录信息为空')
            break
          case -12:
            this.toast('意见反馈内容为空')
            break
          default:
            this.toast('未知错误，请联系管理员')
        }
      })
    },
    _insertFeedBack (memo, callback) {
      MiniSiteApi.insertFeedBack(memo).then(data => {
        callback(data)
      })
    }
  }
}
</script>

<style scoped lang="stylus" rel="stylesheet/stylus">
  @import "~common/stylus/variable"
  @import "~common/stylus/mixin"
  .feedback
    font-size: 0.24rem
    .feedback-wrapper
      height: 100%
      padding: 0.3rem
      background: #FFDA33
    .feedback-inner
      background: #fff
      transition: transform 150ms cubic-bezier(0,0,.21,1)
      transform: translateY(0)
    .feedback-inner-focus
      transform: translateY(-100px)
    .feedback-header
      position: relative
      text-align: center
      padding: 1.1rem 0.42rem 0
      color: #404040
      &:before
        content: ''
        position: absolute
        left: 0
        bottom: -0.2rem
        width: 0.2rem
        height: 0.4rem
        border-radius: 0 0.2rem 0.2rem 0
        overflow: hidden
        z-index: 1
        background-color: #FFDA33
      &:after
        content: ''
        position: absolute
        right: 0
        bottom: -0.2rem
        width: 0.2rem
        height: 0.4rem
        border-radius: 0.2rem 0 0 0.2rem
        overflow: hidden
        z-index: 1
        background-color: #FFDA33
      img
        width: 2.84rem
        height: 2.84rem
      h3
        font-size: 0.32rem
        line-height: 0.46rem
        letter-spacing: 0.67px
        padding-top: 0.6rem
      p
        font-size: 0.28rem
        letter-spacing: 0.58px
        text-align: left
        padding: 0.16rem 0 0.6rem
        line-height: 0.44rem
        border-bottom: 0.03rem dashed #CDCDCD
    .feedback-content
      padding: 0.38rem 0.42rem 0.42rem
      position: relative
      textarea
        display: block
        box-sizing: border-box
        width: 100%
        height: 2rem
        outline: none
        resize: none
        font-size: 0.28rem
        line-height: 0.4rem
        margin: 0
        padding: 0
        border: 0
        word-wrap: break-word
        color: #2f3e4c
        background: #F6F6F6
        padding: 0.24rem 0.28rem
      div
        padding: 0.1rem 0.42rem 0.1rem 0
        color: #d9d9d9
        font-size: 0.24rem
        line-height: 0.42rem
        height: 0.42rem
        overflow: hidden
        background: #F6F6F6
        span
         float: right
    .feedback-submit-wrapper
      padding-bottom: 0.62rem
      text-align: center
    .pem-submit
      display: inline-block
      width: 3.1rem
</style>



